body {
    @include fontRR;
    font-size: 18px;
    line-height: 1.4;
    color: $black;
    @include r(1400) {
        font-size: 16px; }

    &.fixed {
        position: fixed;
        width: 100vw; } }

a {
    text-decoration: none; }

select {
    height: 40px;
    width: 180px;
    padding: 0 20px;
    font-size: 14px;
    border-color: $black;
    border-radius: 50px;
    background: $white url('../img/arrow.png') right .5rem center no-repeat;
    appearance: none;
    cursor: pointer;
    option {
        font-size: 14px; } }

.out {
    position: relative;
    overflow: hidden;
    min-height: 100vh; }
.container {
    position: relative;
    width: 1200px;
    margin: 0 auto;
    @include r(1400) {
        width: 980px; }
    @include r(1100) {
        width: 100%;
        padding: 0 20px; } }

.uppercase {
    text-transform: uppercase; }

.logo {
    display: block;
    position: absolute;
    top: -10px;
    left: 0;

    width: 120px;
    height: 120px;

    background-color: #ffffff;
    box-shadow: 0 2px 10px -5px #000;
    z-index: 2;
    img {
        width: 100%;
        height: 100%; }

    &--bottom {
        top: auto;
        bottom: 10px; }

    @include r(1100) {
        width: 100px;
        height: 100px;
        left: 20px; } }
.line {
    position: relative;
    display: flex;
    justify-content: center;

    width: 100%;

    margin-bottom: 20px;

    svg {
        width: 50px;
        height: 50px;

        fill: $black; }

    &:before, &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);

        width: 45%;
        height: 1px;

        background-color: $black; }

    &:after {
        left: auto;
        right: 0; } }
.main {
    margin-bottom: 200px;

    &__preview {
        margin-bottom: 50px; }

    &__hero {
        margin-bottom: 50px; }

    &__title {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 140px;

        margin-bottom: 40px;

        background: $black url('../img/cake.png') no-repeat top 50% left 50px;
        background-size: 120px;

        border-radius: 5px;

        h2 {
            @include fontRR;
            font-size: 30px;
            color: $white;
            span {
                @include fontRM; } }

        &-img {
            position: absolute;
            right: 50px;
            top: -20px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 180px;
            height: 180px;

            background-color: $white;
            border-radius: 50%;
            box-shadow: 0 5px 10px 0px $black;
            img {
                width: 80%;
                height: 80%; } } }

    &__subtitle {
        @include fontRM;
        font-size: 28px;
        text-align: center;

        margin-bottom: 20px; }

    &__text {
        margin-bottom: 20px;
        &--center {
            text-align: center; }

        span {
            @include fontRM;
            font-size: 24px;
            color: $gold; } }

    @include r(1400) {
        &__title {
            height: 105px;
            background-size: 100px;
            h2 {
                font-size: 20px; }
            &-img {
                width: 140px;
                height: 140px; } } }

    @include r(1100) {
        &__hero {
            display: none; }
        &__title {
            background: $black;
            &-img {
                display: none; } } }

    @include r(768) {
        margin-bottom: 110px;
        &__title {
            margin-bottom: 20px;
            h2 {
                padding: 0 30px;
                text-align: center;
                span {
                    display: block; } } }
        &__subtitle {
            font-size: 22px;
            margin-bottom: 20px; }

        &__preview {
            margin-bottom: 20px; } } }



.input, .textarea {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;

    input, textarea {
        padding: 10px;
        border: 1px solid $black;
        border-radius: 5px;
        width: 400px; }
    textarea {
        height: 150px;
        resize: none; }
    label {
        font-size: 16px;
        margin-bottom: 5px; }

    @include r(768) {
        input, textarea {
            width: 100%; } } }


.textarea {
    margin-bottom: 40px; }

