.photos {

	&__title {
		@include fontRM;
		font-size: 30px;
		text-align: center;

		margin-bottom: 50px;

		a {
			color: $gold; } }

	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }

	&__slider {
			display: none; }
	&__wrap {
		display: flex;
		flex-direction: column; }

	&__wrap &__item {
		width: 175px;
		height: 175px; }


	&__item {
		width: 380px;
		height: 380px;
		margin-bottom: 30px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	@include r(1400) {

		&__item {
			width: 300px;
			height: 300px; }

		&__wrap &__item {
			width: 135px;
			height: 135px; } }

	@include r(768) {

		&__title {
			font-size: 22px;
			margin-bottom: 20px; }

		&__list {
			display: none; }

		&__slider {
			display: block;
			.slick-arrow {
				display: none!important; } }

		&__slide {
			display: flex;
			width: 250px;
			height: 250px;
			padding: 0 20px;
			a {
				width: 100%;
				height: 100%;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover; } } } } }

