.features {
	padding: 50px 0;
	margin-bottom: 50px;
	background: $black url('../img/feature-bg.jpg') no-repeat;
	background-size: cover;

	font-size: 16px;
	color: $white;

	&__title {
		@include fontRM;
		font-size: 30px;
		color: $gold;
		text-align: center;
		margin-bottom: 20px; }

	&__subtitle {
		margin-bottom: 50px;
		text-align: center; }

	&__list {
		display: flex;
		justify-content: space-between; }

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 28%; }
	&__icon {
		display: flex;
		margin-bottom: 15px; }

	@include r(768) {
		padding: 30px 0;
		margin-bottom: 20px;
		&__list {
			flex-direction: column; }

		&__item {
			width: 100%;
			flex-direction: row;
			margin-bottom: 20px;
			justify-content: space-between; }

		&__icon {
			width: 15%;
			margin-bottom: 0;
			img {
				width: 40px; } }

		&__text {
			width: 80%;
			font-size: 14px; }

		&__subtitle {
			font-size: 14px;
			margin-bottom: 30px; }

		&__title {
			font-size: 22px; } } }
