.product {
    margin-bottom: 50px;

    padding: 30px;

    box-shadow: 0 2px 10px -5px $black;
    border-radius: 5px;

    background-color: rgba($black, 0.05);

    &__inner {
        display: flex;
        align-items: center; }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;

        width: 680px;
        margin: 0 auto;
        .product__image {
            display: none; } }


    &__image {

        width: 300px;
        height: 200px;
        background-image: url('../img/macarons.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain; }

    &__title {
        position: relative;
        padding-top: 40px;
        width: 100%;
        margin-bottom: 20px;

        @include fontRB;
        font-size: 24px;
        text-align: center;
        text-transform: uppercase;
        color: $gold;

        &-top {
            position: absolute;
            top: 0;
            left: 0;

            display: flex;
            justify-content: center;

            width: 100%;

            svg {
                width: 30px;
                height: 30px;

                fill: $black; }

            &:before, &:after {
                content: '';
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);

                width: 310px;
                height: 1px;

                background-color: $black; }

            &:after {
                left: auto;
                right: 0; } } }

    &__preview {
        width: 70%;
        margin-bottom: 20px;

        text-align: center; }

    &__price {
        margin-bottom: 20px;

        @include fontRB;
        color: $gold;

        span {
            font-size: 20px;
            @include fontRM;
            &:first-child {
                font-size: 30px; } } }

    &__buttons {
        display: flex;
        justify-content: space-between;
        align-items: center;

        width: 100%;

        margin-bottom: 20px; }

    &__text {
        padding: 0 30px;
        font-size: 12px;
        text-align: center; }

    @include r(900) {

        &__image {
            width: 100%;
            height: 140px;
            display: none; }

        &__content {
            width: auto;
            .product__image {
                display: flex; } }

        &__title {
            font-size: 22px;
            margin-bottom: 20px;
            &-top {
                &:before, &:after {
                    width: 40%; } } }

        &__buttons {
            flex-direction: column;
            & > button {
                margin-top: 15px; } }

        &__preview {
            width: 100%;
            margin-bottom: 10px; }

        &__price {
            margin-bottom: 10px; }

        &__text {
            padding: 0; } } }
