.hero {
    height: 300px;
    color: $white;
    overflow: hidden;
    background-color: $black;
    margin-bottom: 60px;


    &__title {
        height: 300px;
        @include fontRR;
        text-transform: uppercase;
        display: flex;
        margin: auto;
        align-items: center;

        text-align: center; }

    &__inner {
        position: relative;
        display: flex; }


    // &__rectangle

    //     svg
    //         width: 820px
    //         height: 467px

    &__product {}

    &__media {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        img {
            height: 240px;
            object-fit: contain; }
        // position: absolute
        // top: 50px
        // left: 120px
        // width: 100%
        // img
        //     width: 100%
 }        //     object-fit: cover


    @include r(768) {
        height: auto;
        margin-bottom: 20px;
        &__media {
            display: none; }

        &__title {
            height: auto;
            padding: 10px 0;

            font-size: 22px; } } }

