.footer {
    background-color: $black;
    color: $white;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;

    &__inner {
        display: flex;
        justify-content: center;
        padding: 10px 0; }

    &__contact {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 12px;

        a {
            font-size: 20px;
            color: $white;
            transition: 0.3s color ease;

            &:hover {
                color: $gold; } } }

    &__social .social {

        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);

        &__link:hover {
            svg {
                fill: $gold; } }

        svg {
            fill: $white; }

        @include r(1100) {
            right: 30px; } }

    &__copyright {
        padding-bottom: 20px;
        font-size: 12px;
        text-align: center;
        color: $white;
        b {
            font-size: 14px;
            @include fontRM; } }

    @include r(768) {


        &__social, &__copyright {
            display: none; }

        .logo {
            position: static;
            width: 60px;
            height: 60px; }

        &__inner {
            justify-content: space-around; } } }



