.main-nav {
    display: flex;


    &__list {
        display: flex;
        margin: 0 auto; }

    &__item {
        display: flex;
        margin-right: 20px;

        &:last-child {
            margin-right: 0; } }

    &__link {
        font-size: 18px;
        text-transform: uppercase;
        color: $white;
        transition: 0.3s color ease;

        &:hover {
            color: $gold; }

        &.is-active {
            color: $gold; } }

    @include r(1400) {
        &__link {
            font-size: 14px; } }
    @include r(1100) {
        &__list {
            display: none; } } }




.js-main-nav {
    position: fixed;
    left: 0;
    width: 100%;
    bottom: -100vh;
    height: calc(100vh - 110px);
    padding: 50px 30px;
    background-color: $black;
    z-index: 100;
    transition: 0.6s all ease;
    &.is-show {
        display: flex;
        left: 0;
        bottom: 0; }


    & {
        .main-nav__list {
            display: flex;
            flex-direction: column;
            align-items: center; }
        .main-nav__item {
            margin-bottom: 20px;

            a {
                font-size: 18px; } } } }


